.outerWrapper {
    padding-top: 60px;
}

.background {
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: #FDEAA2;
    overflow: hidden;
    z-index: -1;
}

.square {
    width: 70vw;
    height: 70vw;
    position: absolute;
    transform: rotateZ(45deg);
    border-radius: 40px;
    top: -20vw;
    left: -20vw;
    background-color: #FEE188;
}

.circle {
    position: absolute;
    width: 80vw;
    height: 80vw;
    border-radius: 50%;
    background-color: #FEE188;
    left: 60vw;
    top: 80vw;
}

.collegeName {
    text-align: center;
}

.content {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding: 0px 2.5%;
}

.subContent {
    text-align: center;
    margin-top: 20px;
    font-size: 20px;
}

.founderContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 2.5%;
}

.photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 30px 0px;
}

.svg {
    width: 100%;
}

.orangeBox {
    box-sizing: border-box;
    width: 95%;
    min-height: 60vh;
    background-color: orange;
    margin: 30px 2.5%;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.orangeText {
    text-align: center;
    color: white;
}

.whiteText {
    color: orange;
    text-align: center;
    margin: 30px 2.5%;
}

@media screen and (min-width:700px) {
    .square {
        width: 50vw;
        height: 50vw;
    }

    .circle {
        top: 40vw;
    }

    .collegeName {
        font-size: 40px;
    }

    .subContent {
        font-size: 28px;
        margin-top: 60px;
    }

    .svg {
        width: 50%;
        margin-left: 25%;
        /* margin: auto; */
    }
}

@media screen and (min-width:1200px) {
    .svg {
        width: 20%;
        margin-left: 40%;
    }

    .orangeBox {
        width: 40%;
        margin-left: 30%;
    }

    .whiteText {
        width: 40%;
        margin-left: 30%;
    }

    .founderContent {
        width: 40%;
        margin-left: 30%;
        padding: 0;
    }
}